@import url("https://fonts.googleapis.com/css?family=Roboto&display=swap");
@import url("https://fonts.googleapis.com/css?family=Oswald&display=swap");

html {
  height: 100%;
}
body {
  display: flex;
  flex-direction: column;
  height: 100%;
  margin: 0;
  font-family: Roboto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  flex: 1 0 auto;
  & p:last-child {
    margin-bottom: 2rem;
  }
  background: linear-gradient(
    138deg,
    rgb(26, 30, 36) 20%,
    rgb(40, 44, 52) 50%,
    rgb(26, 30, 36) 80%
  );
}

.recharts-responsive-container {
  margin: 0rem auto 1rem auto;
  transition: all 0.1s linear;
  overflow: visible;
}

/*

  tooltip

*/

.recharts-default-tooltip {
  width: 210px;
  background: rgba(33, 37, 43, 0.95);
  text-align: left;
  padding: 0.9rem 1rem;
  box-shadow: 0.1em 0.1em 5em 0em rgba(0, 0, 0, 0.5);
  border: 1px solid #ccc;
  border-radius: 0.4rem;
  /* transform: perspective(300px) rotateY(-2deg); */
  & .recharts-tooltip-label {
    padding: 0;
    margin: 0 auto 0.4rem 0;
    text-align: center;
    font-weight: bold;
  }
  & .recharts-tooltip-item-list {
    margin: 0 auto;
    padding: 0 0.5rem;
    & .recharts-tooltip-item {
      list-style: none;
      margin-bottom: 0.3rem;
      margin-right: 0.5rem;
      font-size: 0.8rem;
      & .recharts-tooltip-item-circle {
        border-radius: 0.5rem;
        width: 2rem;
        height: 0.5rem;
        display: inline-block;
      }
      & .recharts-tooltip-item-name {
        width: 50px;
        padding: 0 0.25rem;
        color: white;
      }
      & .recharts-tooltip-item-value {
        float: right;
        color: white;
        text-align: right;
        border-radius: 5px;
        padding: 0.2rem 0.5rem;
        font-weight: bold;
      }
    }
  }
}

.recharts-surface {
  /* margin-left: -10%; */
  /* transform: perspective(300px) rotateY(-2deg); */
}

/*

  legend and brush

*/

.recharts-legend-wrapper {
  /* transform: perspective(0px) rotateY(0deg); */
}
.recharts-legend-item-text,
.recharts-tooltip-item-name,
.recharts-surface path {
  user-select: none;
  font-family: Oswald;
  text-transform: capitalize;
  transition: all 0.2s linear;
}

.recharts-default-legend li {
  cursor: pointer;
}
.recharts-brush {
  & > rect:first-child {
    fill: rgb(26, 30, 36);
    fill-opacity: 1;
  }
  & .recharts-brush-slide {
    fill: rgb(40, 44, 52);
    fill-opacity: 1;
  }
}

.desactivated {
  display: none;
}

/* recharts lines */

.recharts-line:not(.desactivated) {
  & path {
    transition: stroke-width 0.2s linear, stroke 0.2s linear;
    /* stroke-width: 2; */
  }
  &:has(+ .recharts-active-dot:hover) {
    & path {
      stroke-width: 10;
    }
  }
  &:hover {
    & path {
      stroke-width: 10;
    }
  }
}

.recharts-reference-line text {
  font-size: 0.7rem;
  fill: rgb(26, 30, 36);
  text-transform: none;
  font-style: italic;
  display: none;
  & tspan {
    fill: white;
  }
}

/**/
.scale-switch {
  fill: rgb(128, 128, 128);
  text-transform: capitalize;
  text-decoration: underline;
  cursor: pointer;
  transform: all 1s linear;
  &:hover {
    fill: white;
  }
}
/*

color scale of gravity

*/

.one-yellow {
  background: #aaa;
}
.one-orange {
  background: #aaa;
}
.one-red {
  background: #aaa;
}
.one-black {
  background: #aaa;
}

/*

dashboard

*/

.dashboard {
  display: flex;
  flex-wrap: wrap;

  & .graph {
    min-width: 60%;
    flex-grow: 1;
  }
  & .carto {
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
    margin-bottom: 2rem;
    flex-grow: 1;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
    & .info {
      color: white;

      & .date {
        display: inline-block;
        font-family: Oswald;
        color: rgb(170, 170, 170);
        font-weight: bold;
        font-size: 1.5rem;
        margin: 0 0 1.5rem 0;
        vertical-align: sub;
        padding-left: 1.5rem;
      }
    }
    & .carto-legend {
      color: white;
      font-weight: bold;
      font-size: 0.8rem;
      display: flex;
      margin-top: 1rem;
      & .carto-legend-title {
        margin-right: 1rem;
      }
      & .carto-legend-element {
        & .carto-legend-scale {
          vertical-align: top;
          cursor: mark;
          display: inline-block;
          vertical-align: center;
          width: 2.5rem;
          height: 2.5rem;
          line-height: 2.5rem;
        }
      }
    }
  }
}

/*

carto svg

*/

.france-svg {
  min-height: 320px;
  width: 300px;

  & g {
    stroke: #ccc;
  }

  & text.france-svg-label {
    text-transform: capitalize;
    text-anchor: end;
    font-size: 1rem;
    fill: red;
    stroke: rgb(40, 44, 52);
    stroke-width: 1;

    cursor: pointer;
    & tspan.label-city {
      font-family: Oswald;
      fill: hsl(331, 95%, 56%);
      font-weight: bold;
    }
  }

  & path.station {
    cursor: pointer;
    filter: url(#erode);
    stroke: rgb(40, 44, 52);
    stroke-width: 0px;
    transition: stroke 0.25s;
    &.selected {
      stroke: url(#neon-grad);
      stroke-width: 7px;
      filter: url(#erode);
      transition: stroke 0.25s;
    }
    &.activated {
      filter: url(#erode) url(#neon-overglow);
      transition: stroke 0.25s;
    }
  }
}

/*

aniamtion istemphover

move from selected to activated cycle

*/

@keyframes {
}

.idf-svg {
  min-height: 250px;
}

.counter {
  width: 300px;
  color: #eeeeee;
  font-family: Oswald;
  display: flex;
  align-items: center;
  min-height: 3.2rem;
  & .city,
  & .concentration {
    font-size: 1.4rem;
    text-transform: capitalize;
    opacity: 0%;
    transition: all 1s;
  }

  & .city {
    flex-basis: 55%;
    text-align: right;
    opacity: 100%;
    white-space: nowrap;
  }
  & .concentration {
    padding: 0.5rem 0.75rem;
    border-radius: 0.5rem;
    display: inline-block;
    margin-left: 2rem;
    opacity: 100%;
  }
}

