.reset {
  background: none;
  font-size: 0.9rem;
  color: white;
  font-family: Roboto;
  display: inline-block;
  font-style: normal;
  cursor: pointer;
  margin-left: 0.25rem;
  padding: 0.25rem 0.5rem;
  border-radius: 0.5rem;
  border: 3px solid rgb(102, 102, 102);
  transition: all 0.2s linear;
  &:hover {
    border: 3px solid white;
  }
}

/* mediaqueries */

/* CSS styles for screens with a width greater than 600px */
@media screen and (min-width: 600px) {
  .recharts-reference-line text {
    font-size: 0.7rem;
    display: block;
  }
}

/* CSS styles for screens with a width greater than 800px */
@media screen and (min-width: 800px) {
  .recharts-reference-line text {
    font-size: 0.9rem;
    display: block;
  }
  .dashboard {
    & .graph {
    }
    & .carto {
      flex-grow: 0;
    }
    & header {
    }
  }
}
